#Service {
    display: flex;
}

#Service .item {
    cursor: pointer;
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: var(--ms);
    text-align: center;
}

#Service .item:hover {
    z-index: 1;
    transform: scale(1.1);
}

#Service .item .text * {
    color: white;
}

/* Horizontal */
@media (orientation: landscape) {
    #Service {
        flex-direction: row;
    }

    #Service .item .text {
        padding: 2rem;
    }
}

/* Vertical */
@media (orientation: portrait) {
    #Service {
        flex-direction: column;
    }

    #Service .item .text {
        padding: 1rem;
    }
}